#CLogin{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: #34495e;
    height: 100vh;
}

.Login{
    width: 400px;
    padding: 20px;
    position: absolute;
    top: 50%;
    left:50%;
    transform: translate(-50%,-50%);
    background: #191919;
    text-align: center;
}

.Login h1{
    color: white;
    text-transform: uppercase;
    font-weight: 500;
}

.Login input[type="text"],.Login input[type="password"]{
    border:0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #3498db;
    padding: 14px 10px;
    width: 200px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
}
.Login input[type="text"]:focus,.Login input[type="password"]:focus{
    width: 350px;
    border-color: #2ecc71;
}

.Login input[type="submit"]{
    border:0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #2ecc71;
    padding: 14px 40px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
    cursor: pointer;
}
.Login input[type="submit"]:hover{
    background: #2ecc71;
}

.Login a{
    text-decoration: none;
    color: white;
}