#CLogin{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: #34495e;
    height: 100vh;
}

.Login{
    width: 400px;
    padding: 20px;
    position: absolute;
    top: 50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    background: #191919;
    text-align: center;
}

.Login h1{
    color: white;
    text-transform: uppercase;
    font-weight: 500;
}

.Login input[type="text"],.Login input[type="password"]{
    border:0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #3498db;
    padding: 14px 10px;
    width: 200px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
}
.Login input[type="text"]:focus,.Login input[type="password"]:focus{
    width: 350px;
    border-color: #2ecc71;
}

.Login input[type="submit"]{
    border:0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid #2ecc71;
    padding: 14px 40px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
    cursor: pointer;
}
.Login input[type="submit"]:hover{
    background: #2ecc71;
}

.Login a{
    text-decoration: none;
    color: white;
}
.Containter{
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    width: 40rem;
    height: 100vh;    
    margin-left: auto;
    margin-right: auto;
}

.DataUser{
    width:100%;
    height: 50%;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    flex-wrap: wrap;
    grid-gap:3rem 3rem;
    gap:3rem 3rem;
    background-color:aqua;
    padding-top: 2rem;
    padding-bottom: 2rem;
    border: 1px solid;
    padding-left: 1rem;
    padding-right: 1rem;
   
    
}

.DataUser Datauser_Input,Datauser_Label{
    display: block;
    margin-left: auto;
    margin-right: auto;
    
}

.userImage{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:100%;
    
    
}

.userImagePreview{
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding-left: 3rem;
    
    
}

.userImagePreview label input[type="file"]{
    display: none;
}

.userImagePreview input[type="submit"]{
    width: 3rem;
    margin-left: 1rem
    
}

.userImageSubmit{
width: 50%;

}



#preview{
    width: 6rem;
    height: 6rem;
