    border-radius: 50%;
}

.userImageSubmit{
   text-align: center;
   padding-top: 1rem;
}

.userImageSubmit button{
    width: 5rem;
    height: 4rem;

}

.nav{
    width:100%;
    background-color:#2596be;   
    margin-bottom: 1rem;
    padding-top: 0.4rem;
    height: 2rem;
}

.nav ul{
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.nav_link{
    margin-left: 1rem;
}

.link{
    color:white;
    text-decoration: none;
    padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    transition: all 300ms ease-in-out 0s;
}

.link:hover{
    background-color: #49be25;
    border-radius: 1rem;
    height: 2rem;
}


.formu{
    display: grid;
    background-color: blue;
    grid-template-columns: 1fr 1fr;
    height: 60vh;
    width: 60%;
    margin-left: auto;
    margin-right: auto; 
    margin-top: 10rem;
    
    
}

.archivo{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 60vh;
    background-color: aliceblue;
}

.archivo div label, .archivo div input{
    margin-right: 0.5rem;
}

.img_submit{
    display: grid;
    height: 60vh;
    background-color: aqua;
    grid-template-rows: 1fr 1fr;
}

.img_submit > div > img{
    width: 8rem;
    height: 8rem;
    background-color: blueviolet;
    border-radius: 50%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
}

.img_submit_preview{
    text-align: center;
    width: 100%;
    background-color: burlywood;
    padding-bottom: 1rem;
}

.img_submit_buttons{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    background-color: brown;

}

.img_submit_buttons button{
    width: 5rem;
    height: 4rem;
    border-radius: 1rem;
    cursor: pointer;
}



.textos{
    width: 20rem;
}

.Card_container{
    box-sizing: content-box;
    background-color: coral;
    width: calc(33.33% - 2rem);
