    box-shadow: 0px 3px 9px 1px rgba(0, 10, 20, 0.2);
  }
  .card-top {
    flex-direction: column;
    grid-column: auto / span 1;
    grid-row: auto / span 2;
  }
  .card-image {
    display: flex;
  }
  .card-image img {
    width: 100%;
