    color: #345;
  }
  img{
    min-height: 300px;
    max-height: 300px;
  }
  @media (max-width: 200px) {
    .card-top {
      flex-direction: row;
      grid-column: auto / span 2;
      grid-row: auto / span 1;
    }
    .card-bottom {
      flex-direction: row-reverse;
      grid-column: auto / span 2;
      grid-row: auto / span 1;
    }
    .card-top .card-image,
    .card-bottom .card-image {
      width: 50%;
    }
    .card-top img {
      border-radius: 95.2380952381px 0 0 95.2380952381px;
    }
    .card-bottom img {
      border-radius: 0 95.2380952381px 95.2380952381px 0;
    }
    .card-top .card-text {
      height: auto;
      width: 50%;
      padding-right: 36.8px;
    }
    .card-bottom .card-text {
      height: auto;
      width: 50%;
      padding-left: 36.8px;
    }
  }
  @media (max-width: 200px) {
    .card-image {
      width: 38% !important;
    }
    .card-text {
      width: 62% !important;
    }
  }
